html, body, #root {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

html::-webkit-scrollbar {
  display: none;
}

.app {
  font-family: 'Poppins', sans-serif;
  line-height: 1.5;
  color: var(--clr-fg);
  transition: background-color .2s linear;
  background-color: var(--clr-bg);
  user-select: none;
}

.light {
  --clr-bg: #fcfcfc;
  --clr-bg-alt: #fff;
  --clr-fg: #555;
  --clr-fg-alt: #444;
  --clr-primary: #2978b5;
  --shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.dark {
  --clr-bg: #23283e;
  --clr-bg-alt: #2a2f4c;
  --clr-fg: #bdbddd;
  --clr-fg-alt: #cdcdff;
  --clr-primary: #90a0d9;
  --shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

.background {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transition: background-color .2s linear;
  background-color: var(--clr-bg);
  z-index: -300;
}

main {
  max-width: 1100px;
  margin: 0 auto;
}

.section {
  margin-top: 5em;
}

.section-title {
  text-align: center;
  margin-bottom: 1em;
  text-transform: uppercase;
}

.center {
  display: flex;
  align-items: center;
}

.v-center {
  display: flex;
  justify-content: center;
}

.link {
  color: var(--clr-primary);
  padding: 0 0 0.3em 0;
  position: relative;
}

.link:hover {
  color: var(--clr-primary);
}

.link::before {
  content: '';
  display: inline;
  width: 100%;
  height: 0.2em;
  position: absolute;
  bottom: 0;
  background-color: var(--clr-primary);
  transition: transform 0.15s ease-in;
  transform: scaleX(0);
  transform-origin: center center;
}

.link:hover::before, .link:focus::before {
  transform: scaleX(1);
}

.link-nav {
  color: var(--clr-fg);
  text-transform: lowercase;
  font-weight: 500;
}

.link-icon {
  color: var(--clr-fg);
}

.profile {
  position: absolute;
  padding-bottom: 4em;
}

.btn {
  display: block;
  cursor: pointer;
  padding: 0.8em 1.4em;
  font-weight: 500;
  font-size: 0.9rem;
  text-transform: lowercase;
  transition: transform 0.2s ease-in-out;
}

.btn-outline {
  color: var(--clr-primary);
  border: 2px solid var(--clr-primary);
  position: relative;
  overflow: hidden;
  z-index: 1;
  transition: color 0.2s ease-in-out;
}

.btn-outline:hover, .btn-outline:focus {
  color: var(--clr-bg);
}

.btn-outline:before {
  content: '';
  position: absolute;
  background-color: var(--clr-primary);
  right: -10px;
  bottom: -10px;
  left: -10px;
  top: -10px;
  z-index: -1;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}

.btn-outline:hover:before, .btn-outline:focus:before {
  opacity: 1;
}

.btn-plain {
  text-transform: initial;
  background-color: var(--clr-bg-alt);
  box-shadow: rgba(0, 0, 0, 0.15) 0px 3px 3px 0px;
  border: 0;
}

.btn-plain:hover {
  transform: translateY(-4px);
}

.btn-icon {
  padding: 0;
}

.btn-icon:hover, .btn-icon:focus {
  color: var(--clr-primary);
}

.btn-icon:active {
  transform: translateY(-5px);
}

.fade-enter {
  opacity: 0;
  z-index: 1;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity .1s ease-in;
}

.fade-exit {
  opacity: 1;
  z-index: 0;
}

.fade-exit.fade-exit-active {
  opacity: 0;
  transition: opacity .1s ease-out;
}

@media (max-width: 600px) {
  .section {
    margin-top: 4em;
  }
}